<template lang="html">
  <div class="phases_details">
    <el-table
    :data="tableData"
    v-loading="loading"
    border
    element-loading-text=""
    style="width: 100%">
      <el-table-column
        align="center"
        prop="id"
        label=""
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        align="center"
        prop="project_name"
        label=""
        width="300"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        align="center"
        prop="phase_name"
        label=""
        width="300"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        align="center"
        prop="status"
        label=""
        width="200"
        show-overflow-tooltip>
      </el-table-column>
      </el-table-column>
      <el-table-column
        align="center"
        fixed="right"
        label="">
        <template scope="scope">
          <el-button @click="midClick(scope.row.id)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <v-pages :total="total" v-on:currentChange="nextPhases"></v-pages>
  </div>
</template>

<script>
import pages from '../components/pages/pages.vue'
export default {
  data() {
    return {
      loading: false,
      tableData: [],
      form: {

      },
      total: 1
    }
  },
  created() {
    this.nextPhases(1)
  },
  methods: {
    //  
    nextPhases(page) {
      var _this = this
      $.ajax({
        url: '/admin/api/v1/projects_phases?page=' + page,
        beforeSend: function() {
          _this.loading = true
        },
        timeout: 5000,
        success: function(result) {
          _this.loading = false
          var data = result.result
          for (var i in data.items) {
            var status = data.items[i].status
            switch (status) {
              case 'Paid':
                data.items[i].status = ''
                break;
              case 'Canceled':
                data.items[i].status = ''
                break;
              case 'Submitting':
                data.items[i].status = ''
                break;
              case 'Submitted':
                data.items[i].status = ''
                break;
              case 'Confirmed':
                data.items[i].status = ''
                break;
              case 'Finished':
                data.items[i].status = ''
                break;
            }
          }
          _this.total = data.total
          _this.tableData = data.items
        },
        complete: function(XMLHttpRequest, status){ //
    if(status == 'timeout'){ //,statussuccess,error
              _this.loading = false
      _this.$message.error('')
    }
        },
        error: function(err) {
          if (err.status == '401') {
            _this.$message.error(JSON.parse(err.responseText).message)
            _this.$router.push('/admin/signin')
          }
        }
      })
    },
    //  
    midClick(id) {
      this.$router.push({path: '/admin/nextAtta', query: id})
    },
  },
  components: {
    'v-pages': pages
  }
}
</script>

<style lang="scss">

</style>



